import { setHours, setMinutes } from 'date-fns'
import { Observer } from 'mobx-react-lite'
import React from 'react'
import { Form } from 'react-form'
import styled from 'styled-components'

import { FormInputDate } from '../../../atoms/FormInputDate'
import { FormInputTextBox } from '../../../atoms/FormInputTextBox'
import { FormInputTimeSpan } from '../../../atoms/FormInputTimeSpan'
import { useRoot } from '../../../atoms/RootProvider'
import { fullDateFormat } from '../../../helpers/formats'
import { i18n } from '../../../i18n'
import { FormSubmit } from '../../../molecules/FormSubmit'
import {
  ESpecificHourMode,
  ISpecificFormData,
} from '../models/BusinessHoursModel'
import { SpecificModeSelector } from '../molecules/SpecificModeSelector'

const DateContainer = styled.div`
  display: inline-block;
  width: 13rem;
  margin-left: 1rem;
`

const FormContainer = styled.div`
  display: inline-block;
  width: 16rem;
  min-height: 6rem;
  margin: 0rem 0 0 1rem;
  vertical-align: top;
`

export const SpecificBusinessHourForm: React.FC = props => {
  const {
    settings: { businessHours },
  } = useRoot()
  return (
    <Form
      onSubmit={businessHours.submitSpecificHour}
      defaultValues={getDefaultValues()}
    >
      <>
        <DateContainer>
          <FormInputDate
            field="date"
            dateFormat={fullDateFormat}
            minDate={new Date()}
            inputWidth="12rem"
          />
        </DateContainer>

        <FormContainer>
          <SpecificModeSelector />
          <br />
          <Observer>
            {() =>
              businessHours.specificMode === ESpecificHourMode.TIMESPAN ? (
                <FormInputTimeSpan fieldFrom="openAt" fieldTo="closeAt" />
              ) : (
                <FormInputTextBox
                  field="note"
                  placeholder={i18n.t`Closing reason`}
                  inputWidth={13}
                />
              )
            }
          </Observer>
        </FormContainer>

        <FormSubmit width="9rem">{i18n.t`Add`}</FormSubmit>
      </>
    </Form>
  )
}

function getDefaultValues(): Partial<ISpecificFormData> {
  const now = new Date()
  return {
    date: now,
    openAt: setHours(setMinutes(now, 0), 9),
    closeAt: setHours(setMinutes(now, 0), 20),
  }
}
